@import url(https://fonts.googleapis.com/css2?family=Titillium+Web&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Alegreya&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Titillium+Web&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.tech__logos {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  background-color: #eff8ff;
  border: solid 2px #aad4f5;
  margin-bottom: 15%;
  padding: .5em;
}

.tech__logos i{
  font-size: 7em;
}

.techstack__title {
  color: #fff5aa;
  display: flex;
  justify-content: center;
  font-size: 20px;
  margin-top: 2em;
}

.techstack__wrapper {
  margin-top: 3em;
}

@media (max-width: 1200px) {
  .tech__logos i {
    font-size: 6em;
  }
}
@media (max-width: 1100px) {
  .tech__logos i {
    font-size: 5em;
  }
}
@media (max-width: 800px) {
  .tech__logos i {
    font-size: 4em;
  }
}
@media (max-width: 500px) {
  .tech__logos i {
    font-size: 4em;
  }
}
.homepage__location {
  color: #12283a;
  font-weight: 800;
  font-family: 'Titillium Web', sans-serif;
}

.homepage__wrapper {
  margin: 5% 0 3% 0;
  display: flex;
  left: 85px;
  justify-content: center;
}


.homepage__title {
  color: #2368a2;
  font-size: 2em;
  font-family: 'Alegreya', serif;
}

.homepage__name {
  font-family: 'Titillium Web', sans-serif;
  font-weight: 800;
  font-size: 3em;
  color: #3183c8;
}

.portrait {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  width: 300px;
  height: 300px;
  border-radius: 150px;
  margin-right: 2em;
  -webkit-filter: drop-shadow(15px -6px 5px #88888f);
          filter: drop-shadow(15px -6px 5px #88888f);
}

.portrait img {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.footer__author {
  color: white;
}

.homepage__contact {
  display: flex;
  margin-top: 3em;
  width: 50px;
}

.homepage__info__wrapper {
  margin-top: 4em;
}

@media (max-width: 500px) {
  .homepage__location {
    text-align: center;
  }

  .homepage__info__wrapper {
    margin-top: 0;
  }

  .portrait {
    margin: 0;
  }

  .techstack__title {
    display: none;
  }

  .homepage__location {
    text-align: center;
  }
  
}



.portfolio__header {
  display: flex;
}

.portfolio__header {
  justify-content: center;
  font-size: 2em;
}

.portfolio__experience {
  display: flex;
  justify-content: center;
  margin: 0 5em 5em 5em;
}

.portfolio__experience--category {
  text-align: center;
  color: #2368a2;
}

.portfolio__experience__card,
.portfolio__education,
.portfolio__work {
  background-color: #eff8ff;
  border: solid 2px #aad4f5;
  padding: 1em;
  margin: 1em;
  text-align: center;
}

.portfolio__experience__card {
  flex-basis: 30%;
}

.portfolio__education,
.porfolio__work {
  flex-basis: 50%;
}



.portfolio__experience--title,
.portfolio__wrapper--title {
  font-family: "Titillium Web", sans-serif;
  font-weight: 800;
  text-align: center;
  color: #63a2d8;
  margin-top: 1em;
}

.cv__wrapper {
  justify-content: space-between;
}

.portfolio__wrapper {
  display: flex;
  justify-content: center;
}

.portfolio__page {
  margin-bottom: 15%;
}

.portfolio__work,
.portfolio__education {
  margin-top: 2em;
  background-color: #eff8ff;
  border: solid 2px #aad4f5;
}
.portfolio__work p,
.portfolio__education p {
  font-size: 17px;
}

.portfolio__experience--category__wrapper {
  display: flex;
  justify-content: space-between;
}

.iconify {
  width: 50px;
}

@media (max-width: 1500px) {
  .portfolio__education img {
    width: 1000px;
  }
}

@media (max-width: 1300px) {
  .portfolio__education img {
    width: 800px;
  }
}

@media (max-width: 1200px) {
  .portfolio__experience,
  .portfolio__wrapper {
    flex-wrap: wrap;
  }

  .portfolio__education,
  .portfolio__work {
    flex-basis: 70%;
  }

  .portfolio__experience {
    margin: 0;
  }
}

@media (max-width: 1000px) {
  .portfolio__education img {
    width: 600px;
  }
}

@media (max-width: 700px) {
  .portfolio__education img {
    width: 400px;
  }
  .portfolio__work,
  .portfolio__education {
    flex-basis: 30%;
  }
}

@media (max-width: 500px) {
  .portfolio__work p,
  .portfolio__education p {
    font-size: 12px;
  }

  .portfolio__experience {
    margin: 0;
  }

  .portfolio__experience--category {
    font-size: 20px;
  }

  .portfolio__experience--title,
  .portfolio__wrapper--title {
    font-size: 20px;
    margin: 15px 0 0 8px;
  }

  .portfolio__experience,
  .portfolio__wrapper {
    flex-direction: column;
  }

  .portfolio__education img {
    width: 300px;
  }
}

.cv__wrapper {
  display: flex;
  color: white;
}

.cv__wrapper p {
  margin: 1em;
  color: #343a40;
}

.cv__date {
  font-weight: 650;
  color: #1a4971;
}

.project__wrapper,
.project__card {
  display: flex;
  justify-content: space-evenly;
}

.project__card {
  border: solid 1px white;
  flex-basis: 30%;
  border: solid 2px #aad4f5;
  color: #2368a2;
  background-color: #eff8ff;
}

.project__card:hover {
  box-shadow: 7px 7px 5px 0px  rgba(46, 45, 44, 0.452);
}

.project__text {
  font-size: 15pt;
}

.project__topic {
  color: orange;
}

.project__title {
  text-align: center;
  font-size: 1.4em;
}

.project__wrapper {
  font-family: 'Titillium Web', sans-serif;
  margin-top: 1%;
  display: flex;
  flex-wrap: wrap;
  padding-bottom: 10em;
}

.project__image {
  object-fit: cover;
  width: 100%;
  height: 500px;
}

.project__card {
  margin: 1em;
}

.project__card:hover {
  transform: scale(1.01);
}

.button__wrapper {
  margin: 1em;
  display: flex;
  justify-content: space-evenly;
}

.project__title {
  text-align: center;
  color: #63a2d8;
  margin: 1em;
  font-family: 'Titillium Web', sans-serif;
  font-weight: 800;
}

.link__button {
  border-radius: 25%;
  margin-bottom: 1em;
}

.btn:hover {
  color: green;
}

.card-text {
  padding: 5px;
}

@media (max-width: 1200px) {
  .project__card {
    flex-basis: 85%;
  }
}

@media (max-width: 500px) {
  .project__wrapper {
    flex-direction: column;
    align-items: center;
  }
 
  .project__card {
    margin: 1em;
  }

  .project__image {
    height: 300px;
  }

  .project__title {
    font-size: 20px;
  }
}

